'use client';

import Link from 'next/link';
import { useEffect, useState } from 'react';

export default function NavBar() {
  const [isAdmin, setIsAdmin] = useState(false);

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem('user') || '{}');
    setIsAdmin(user.role === 'admin');
  }, []);

  return (
    <nav className="navbar">
      <div className="container nav-content">
        <Link href="/" className="brand-logo">
          
          <span className="logo-text">Inventory Management</span>
        </Link>
        <div className="nav-links">
          <Link href="/" className="nav-link">Dashboard</Link>
          <Link href="/inventory" className="nav-link">Inventory</Link>
          <Link href="/transactions" className="nav-link">Transactions</Link>
          <Link href="/history" className="nav-link">History</Link>
          {isAdmin && <Link href="/members" className="nav-link">Members</Link>}
        </div>
      </div>
    </nav>
  );
}
